import server from '../../server.js';
export default {
	data() {
		return {
			shadow:true,
			isShowAD:true,
			modalName:''
		}
	},
	onLoad:function(){
		uni.getStorage({
		    key: 'showAD',
		    success: (res) => {
				console.log(res.data)
		        if(res.data=='true'){
					this.isShowAD=true 
					this.shadow = true
				}else{
					this.isShowAD=false
					this.shadow = false
				}
		    }
		});
		if(server.userinfo.VIPEndTime>0){
			this.isShowAD=false;
		}else{
			this.isShowAD=true;
			this.shadow = false
		}	
	},
	methods: {
		getyinsi:function(){
			uni.navigateTo({
				url: '../edit/edit?type=5'
			})
		},
		kaitong:function(){
			this.modalName=''
			uni.navigateTo({
				url: '../vip/vip'
			})
		},
		userAnquan:function(){
			uni.navigateTo({
				url: '../anquan/anquan'
			});
		},
		logout:function(){
			uni.clearStorage();
			uni.showToast({
				title: "即将退出登录",
				position:'bottom',
				icon:'none',
				position:'center'
			})
			server.userinfo=[];
			server.cookie="";
			setTimeout(function () {
				uni.redirectTo({
					url: '../Home/Home'
				})
			}, 2000);
		},
		SetShadow(e) {
			this.shadow = e.detail.value
			if(this.shadow==false){
				console.log("先冲会员")
				if(server.userinfo.VIPEndTime>0){
					uni.setStorage({
					    key: 'showAD',
					    data: 'false',
					    success: function () {
					        console.log('success');
					    }
					});
				}else{
					this.modalName = 'Image'
					uni.setStorage({
					    key: 'showAD',
					    data: 'true',
					    success: function () {
					        console.log('success');
					    }
					});
				}
			}else{
				uni.setStorage({
				    key: 'showAD',
				    data: 'true',
				    success: function () {
				        console.log('success');
				    }
				});
			}
			
			console.log(this.shadow)
		},
		guanbi:function(){
			if(server.userinfo.VIPEndTime<=0){
				this.modalName = 'Image'	
			}
		},
		getInfo:function(){
			uni.navigateTo({
				url: '../editinfo/editinfo'
			});
		},
		getAbout:function(){
			uni.navigateTo({
				url:'../about/about'
			})
		}
	}
}
